<template>
    <div>
        <div class="shishi">
            <div v-if='!isLogin'>
                <van-tabs v-model="active" animated swipeable>
                    <van-tab title="全部">
                        <div class="order-list" v-for='(item,index) in orderData' :key='index'>
                            <div class="list-top clearfix">
                                <span class="fl">{{item.data[0].oid}}</span>
                                
                                <span class="fr list-icon" v-if='item.data[0].status == 2' @click='delOrder(item.data[0])'>
                                    <van-icon class="delete-icon" name="delete" />
                                </span>
                                <span class="fr" @click='item.data[0].status == 1?endOrder(item.data[0]):null'>{{item.data[0].status == 2 ? a : b  }}</span>

                            </div>
                            <hr>
                            
                            <div class="shoplist clearfix" v-for='(v,i) in item.data' :key='i'>
                                <div class="list-img fl">
                                    <img :src="v.smallImg" alt="">
                                </div>
                                <div class="fl list-info">
                                    <div>{{v.name}}</div>
                                    <div>{{v.enname}}</div>
                                    <div>{{v.rule}}</div>
                                </div>
                                <div class="fr list-all ">
                                    <p>￥{{v.price}}</p>
                                    <p class="fr">X{{v.count}}</p>
                                </div>
                            </div>

                            <div class="clearfix list-bottom">
                                <div class="list-time fl">{{item.date}}</div>
                                <div class="alllist-price fr">
                                    <span>共计{{item.count}}件商品</span><span>合计:</span><span>￥{{item.allprice}}</span>
                                </div>
                            </div>
                        </div>
                    </van-tab>
                    <van-tab title="待收货">
                        <div class="order-list" v-for="(item,index) in orderDatastr" :key='index'>
                            <div class="list-top clearfix">
                                <span class="fl">{{item.data[0].oid}}</span>
                                
                                
                                <span class="fr" @click='endOrder(item.data[0])'>确认收货</span>

                            </div>
                            <hr>
                            
                            <div class="shoplist clearfix" v-for='(v,i) in item.data' :key='i'>
                                <div class="list-img fl">
                                    <img :src="v.smallImg" alt="">
                                </div>
                                <div class="fl list-info">
                                    <div>{{v.name}}</div>
                                    <div>{{v.enname}}</div>
                                    <div>{{v.rule}}</div>
                                </div>
                                <div class="fr list-all ">
                                    <p>￥{{v.price}}</p>
                                    <p class="fr">X{{v.count}}</p>
                                </div>
                            </div>

                            <div class="clearfix list-bottom">
                                <div class="list-time fl">2020-06-28 22:58:36</div>
                                <div class="alllist-price fr">
                                    <span>共计{{item.count}}件商品</span><span>合计:</span><span>￥{{item.allprice}}</span>
                                </div>
                            </div>
                        </div>
                    </van-tab>
                    <van-tab title="已收货">
                        <div class="order-list" v-for='(item,index) in orderDataend' :key='index'>
                            <div class="list-top clearfix">
                                <span class="fl">{{item.data[0].oid}}</span>
                                
                                <span class="fr list-icon" v-if='item.data[0].status == 2' @click="delOrder(item.data[0])">
                                    <van-icon class="delete-icon" name="delete" />
                                </span>
                                <span class="fr">{{item.data[0].status == 2 ? a : b  }}</span>

                            </div>
                            <hr>
                            
                            <div class="shoplist clearfix" v-for='(v,i) in item.data' :key='i'>
                                <div class="list-img fl">
                                    <img :src="v.smallImg" alt="">
                                </div>
                                <div class="fl list-info">
                                    <div>{{v.name}}</div>
                                    <div>{{v.enname}}</div>
                                    <div>{{v.rule}}</div>
                                </div>
                                <div class="fr list-all ">
                                    <p>￥{{v.price}}</p>
                                    <p class="fr">X{{v.count}}</p>
                                </div>
                            </div>

                            <div class="clearfix list-bottom">
                                <div class="list-time fl">2020-06-28 22:58:36</div>
                                <div class="alllist-price fr">
                                    <span>共计{{item.count}}件商品</span><span>合计:</span><span>￥{{item.allprice}}</span>
                                </div>
                            </div>
                        </div>
                    </van-tab>
                </van-tabs>
            </div>
            <div v-else>
                <van-empty description="" />
                <div class="login-null">请先<span @click="toLogin">登入</span></div>
            </div>
        </div>
    </div>
</template>

<script>
import '../../assets/css/order.less'
    export default {
        data() {
            return {
                isLogin:false,
                a:'已完成',
                b:'确认收货',
                active: 0,
                oidData:[],
                orderData:[],
                orderDatastr:[],
                orderDataend:[]
            };
        },
        created(){
            if(localStorage.getItem('login_Tk')){
                this.isLogin=true
                this.getOrder();
            }
            
            // this.changeOrder();
        },
        methods:{
            toLogin(){
                this.$router.push({name:'Login'})
            },
            //获取订单数据
            getOrder(){
                let tokenString = localStorage.getItem('login_Tk');
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                this.axios({
                    method:'GET',
                    url:'/findOrder',
                    params:{
                        appkey:this.appkey,
                        tokenString,
                        status:0
                    }
                }).then(result=>{
                    this.$toast.clear();
                    
                    if(result.data.code == 70000){
                        //降序排序
                        result.data.result.sort((a, b) => {
                            return new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime();
                        })

                        result.data.result.forEach(v => {
                            if (this.oidData.indexOf(v.oid) === -1) {
                            this.oidData.push(v.oid);
                            }
                        })

                        let objdata = {};

                        result.data.result.forEach(v=>{
                            if(!objdata[v.oid]){
                                objdata[v.oid]={
                                    data:[v],
                                    status:v.status,
                                    date:v.createdAt,
                                    count:v.count,
                                    allprice:v.count * v.price
                                }
                            }else{
                                objdata[v.oid].count += v.count;
                                objdata[v.oid].allprice += v.count * v.price;
                                objdata[v.oid].data.push(v);
                            }
                        })
                        
                        
                        let arrdata = []
                        for(let key in objdata){
                            arrdata.push(objdata[key])
                        }
                        
                        this.orderData = arrdata
                        this.changeOrder();
                    }
                    
                    
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
            },
            //确认收货
            endOrder(item){
                let tokenString = localStorage.getItem('login_Tk');
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                this.axios({
                    method:'POST',
                    url:'/receive',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        oid:item.oid
                    }
                }).then(result=>{
                    this.$toast.clear();
                    
                    if(result.data.code == 80000){
                        item.status = 2;
                        this.changeOrder();
                    }
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
            },
            //重新遍历数组
            changeOrder(){
                this.orderDatastr=[];
                this.orderDataend=[];
                
                
                for(let key in this.orderData){
                    
                    if(this.orderData[key].data[0].status==1){
                        this.orderDatastr.push(this.orderData[key])
                    }else{
                        this.orderDataend.push(this.orderData[key])
                    }
                }
                
                
                    
                    
                
            },
            delOrder(item){
                
                let tokenString = localStorage.getItem('login_Tk');
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,

                    //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
                    duration: 0
                });
                this.axios({
                    method:"POST",
                    url:'/removeOrder',
                    data:{
                        appkey:this.appkey,
                        tokenString,
                        oid:item.oid
                    }
                    
                }).then(result=>{
                    this.$toast.clear()
                    
                    if(result.data.code==90000){
                        
                        let a = item.oid
                        for(let i=0 ; i < this.orderData.length ; i++){
                            if(a == this.orderData[i].data[0].oid){
                                this.orderData.splice(i,1)
                                break;
                            }
                        }
                        this.getOrder();
                    }
                }).catch(err=>{
                    this.$toast.clear()
                    
                })
            }
        }
    }
</script>

<style lang="less" scoped>
.login-null{
    font-size: 16px;
    text-align: center;
    >span{
        color: skyblue;
    }
}
/deep/ .van-tabs__content--animated {
    overflow-y: scroll;
    overflow-x: hidden;
    height: 570px;
}
/deep/ .van-tabs__content--animated::-webkit-scrollbar {
    display: none;
}
/deep/.van-tabs__nav--line{
   
    background-color: #f4b973;
}
</style>